.container {
	width: 320px;
}

.title {
	font-size: 30px;
	line-height: 35px;
	margin-top: 60px;
}

.ui-tabs .ui-tabs-nav {
	height: 43px;
}

.ui-tabs .ui-tabs-nav li .ui-tabs-anchor {
	font-size: 14px;
	line-height: 16px;
	padding: 12px 18px 13px;
}

.ui-selectmenu-button.ui-button {
	margin-bottom: 20px;
}

.ui-selectmenu-text {
	font-size: 16px;
	line-height: 19px;
}

.ui-selectmenu-menu .ui-menu li.ui-menu-item > div {
	font-size: 16px;
	line-height: 19px;
}

.checkbox input:checked + .checkbox__text:after {
	top: -1px;
}


