.production-banner {
	padding-top: 69px;
	height: 417px;
}

.production-banner__title {
	font-size: 36px;
	line-height: 42px;
	margin-top: 0;

}

.production-banner__par {
	font-size: 16px;
	line-height: 24px;
}

.production-banner__sguares {
	right: -537%;
	top: 26px;
}

.production-banner__ellipse {
	width: 396px;
	height: 410px;
	left: -152px;
	top: 15px;
}


//list

.production-list {
	.btn {
		width: 100%;
	}
}

.production-item {
	flex-direction: column;
	position: relative;
	padding-top: 80px;
}

.production-item:first-child {
	padding-top: 80px;
}

.production-item__info {
	width: 100%;
}

.production-item__desc {
	padding-bottom: 297px;
	max-width: 98%;
}

.production-item__img-wrap {
	width: 100%;
	position: absolute;
	top: 376px;
}

.production-item__img {
	height: 176px;
	& > img {
		max-height: 250px;
	}
}

.production-item--reverse {
	.production-item__info {
		padding-left: 0;
	}
}

.production-item:nth-child(2) {

	.production-item__img-wrap {
		top: 469px;
	}

	.production-item__desc {
		padding-bottom: 316px;
	}
}

.production-item:nth-child(3) {
	.production-item__img-wrap {
		top: 429px;
	}

	.production-item__desc {
		padding-bottom: 299px;
	}
}

.production-item:nth-child(4) {
	.production-item__img-wrap {
		top: 524px;
	}

	.production-item__desc {
		padding-bottom: 302px;
	}
}

.production-list {
	padding-bottom: 71px;
}
