.article__title {
	font-size: 24px;
	line-height: 32px;
	margin-top: 60px;
	margin-bottom: 20px;
}

.sidebar {
	display: none;
}

.article {
	width: 100%;
}

.article__img {
	height: 286px;
	margin-bottom: 10px;
	& > img {
		border-radius: 3px;
		object-fit: cover;
		height: 100%;
		width: 100%;
	}
}

.article__info-item-title {
	font-size: 12px;
	line-height: 28px;
}

.article__info {
	justify-content: space-between;
	margin-bottom: 30px;
}
.article__info-item {
	width: auto;
}

.article__info-item-desc {
	font-size: 16px;
	line-height: 24px;
}

.article__par {
	font-size: 16px;
	line-height: 24px;
	margin: 10px 0 16px;
}

.article__list {
	padding: 13px 0 0 15px;
}

.article__list > li {
	font-size: 16px;
	line-height: 24px;
	margin-bottom: 16px;
}

.article__subtitle {
	font-size: 18px;
	line-height: 28px;
	margin-top: 54px;
	margin-bottom: 30px;
}

.article__desc {
	font-size: 18px;
	line-height: 26px;
	margin-top: 30px;
	max-width: 95%;
}

.article__block {
	margin-top: 30px;
	margin-bottom: 30px;
	font-size: 16px;
	line-height: 24px;
	padding-left: 15px;
	padding-top: 10px;
	padding-bottom: 10px;
	max-width: 95%;
	&::before {
		left: 0;
	}
}

.article__social-wrap {
	margin-top: 64px;
}

.article__social-title {
	font-size: 18px;
	line-height: 21px;
	margin-right: 41px;
}

.article__social > a > svg {
	max-width: 18px;
	max-height: 18px;
}

.article__title-bottom {
	font-size: 30px;
	line-height: 120%;
	margin: 60px 0 20px;
}
.useful-articles__bottom {
	margin-top: 20px;
}

.article__wrapper {
	padding-bottom: 55px;
}
