.breadcrumbs {
	margin-top: 20px;
	margin-bottom: 25px;
}

.breadcrumb {
	font-size: 10px;
	line-height: 12px;
}

a.breadcrumb::after {
	font-size: 10px;
	line-height: 12px;
}

.card {
	.btn {
		width: 100%;
	}
}

.card__wrap {
	position: relative;
}

.card-info {

}

.card__info-wrap {
	width: 100%;
}

.card__img {
	position: absolute;
	top: 70px;
	width: 100%;
	padding-top: 0;
	& > img {
		max-width: 210px;
		max-height: 275px;
	}
}

.card__title {
	font-size: 36px;
	line-height: 42px;
	margin-top: 0;
	padding-bottom: 299px;
}

.card__subtitle {
	font-size: 18px;
	line-height: 21px;
}

.card__par {
	font-size: 14px;
	line-height: 20px;
	margin-bottom: 29px;
}

.card__min-subtitle {
	margin-top: 49px;
	margin-bottom: 19px;
}

.card__list {
	li {
		font-size: 14px;
		line-height: 20px;
		margin-bottom: 15px;
		max-width: 98%;
	}
}

.card__list li::before {
	font-size: 14px;
	line-height: 20px;
}

.ui-tabs .ui-tabs-nav.card-info__tabs {
	height: max-content;
	display: flex;
	flex-direction: column;
}

.ui-tabs .ui-tabs-nav.card-info__tabs li.card-info__tab {
	margin-bottom: 10px;
}

.ui-tabs .ui-tabs-nav.card-info__tabs li.card-info__tab.ui-tabs-active {
	margin-bottom: 10px;
	.ui-tabs-anchor {
		font-weight: 500;
	}
}

.ui-tabs .ui-tabs-nav.card-info__tabs li.card-info__tab .ui-tabs-anchor {
	padding: 0 0 10px;
	font-weight: normal;
}


.card-info__specifications-item {
	margin-bottom: 10px;
}

.card-info__specifications-item-title {
	font-size: 16px;
	line-height: 19px;
	width: 135px;
}

.card-info__specifications-item-desc {
	width: calc(100% - 135px);
	font-size: 14px;
	line-height: 20px;
}

.card-info__safety-precautions__list {
	min-width: 100%;
}

.card-info__safety-precautions__item {
	margin-bottom: 13px;
}

.card-info__safety-precautions__item-number {
	font-size: 16px;
	line-height: 26px;
}

.card-info__safety-precautions__item-text {
	font-size: 14px;
	line-height: 20px;
}

.card-info__contraindications {
	padding-top: 20px;
	font-size: 14px;
	line-height: 20px;
}
.card-info__certificates {
	padding-top: 20px;
}

.card-info__certificates .documents__document {
	margin-bottom: 10px;
}


.card-instruction__title {
	margin-top: 7px;
}

.card-instruction__video {
	height: 122px;
	& > img {

	}
}

.card-maps {
	.partnership {
		margin-top: 0;
	}

	.partnership__title {
		margin-top: 51px;
	}
}
