.production-banner {
	padding-top: 99px;
	height: 380px;
}

.production-banner__title {
	font-size: 48px;
	line-height: 56px;
	margin-bottom: 20px;
}

.production-banner__par {
	font-size: 16px;
	line-height: 26px;
	max-width: 610px;
}

.production-banner__sguares {
	right: -57%;
	top: 41px;
}

.production-banner__ellipse {
	width: 947px;
	height: 947px;
	left: 0;
}


//list

.production-list {
	.btn__text {
		display: flex;
		align-items: center;
	}
}

.production-item {
	padding-top: 310px;
}

.production-item__info {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}

.production-item__img-wrap {
	display: flex;
	justify-content: flex-end;
}

.production-item__img {
	height: 340px;
	width: 90%;
	& > img {
		max-height: 444px;
	}
}

.production-item--reverse {
	.production-item__img-wrap {
		justify-content: flex-start;
	}
}

