.breadcrumbs {
	margin-top: 30px;
	margin-bottom: 30px;
}

.breadcrumb {
	display: flex;
	height: 16px;
	font-size: 12px;
	line-height: 14px;
}
a.breadcrumb {
	&::after {
		display: flex;
		height: 16px;
		font-size: 12px;
		line-height: 14px;
	}
}

.card {
	padding-bottom: 65px;
}

.card__wrap {
	position: relative;
}

.card-info {
	padding-bottom: 60px;
}

.card-info__wrap {
	width: 100%;
	flex-wrap: wrap;
	justify-content: flex-end;
}

.card__info {
	display: flex;
	justify-content: flex-end;
}

.card__info-wrap {
	width: 50%;
}
.card__info-two {
	width: 100%;
}

.card__img {
	padding-top: 100px;
	position: absolute;
	top: 0;
	left: 0;
	& > img {
		max-width: 220px;
		max-height: 300px;
	}
}

.card__title {
	margin-top: 17px;
	font-size: 36px;
	line-height: 42px;
}

.card__subtitle {
	font-size: 20px;
	line-height: 23px;
	margin-bottom: 20px;
}

.card__par {
	font-size: 16px;
	line-height: 24px;
}

.card__min-subtitle {
	font-size: 14px;
	line-height: 16px;
	margin-top: 39px;
	margin-bottom: 21px;
}

.card__list {
	li {
		font-size: 16px;
		line-height: 24px;
		margin-bottom: 14px;
		max-width: 453px;
	}
}

.ui-tabs .ui-tabs-nav.card-info__tabs {
	height: 25px;
}

.ui-tabs .ui-tabs-nav.card-info__tabs li.card-info__tab .ui-tabs-anchor {
	font-size: 12px;
	line-height: 14px;
	padding: 0 17px 10px;
}

.ui-tabs .ui-tabs-nav.card-info__tabs li.card-info__tab:first-child .ui-tabs-anchor {
	padding-left: 0;
}

.ui-tabs .ui-tabs-nav.card-info__tabs li.card-info__tab:last-child .ui-tabs-anchor {
	padding-right: 0;
}

.card-info__specifications-item-title {
	font-size: 18px;
	line-height: 21px;
	flex-shrink: 0;
	margin-right: 0;
}

.card-info__specifications-item-desc {
	font-size: 16px;
	line-height: 19px;
	width: calc(100% - 300px);
}
.card-info__safety-precautions__item-number {
	font-size: 18px;
	line-height: 21px;
}
.card-info__safety-precautions__item-text {
	font-size: 16px;
	line-height: 19px;
	margin-left: 30px;
}

.card-info__contraindications {
	font-size: 16px;
	line-height: 19px;
}

.card-info__specifications-list {
	padding-top: 20px;
}

.card-info__specifications-item {
	margin-bottom: 22px;
	align-items: flex-start;
}

.card-instruction__title {
	font-size: 14px;
	line-height: 16px;
}

.card-instruction__video {
	width: 100%;
	height: 260px;
	margin-bottom: 20px;
	& > img {
		border-radius: 3px;
		object-fit: cover;
		height: 100%;
		width: 100%;
	}
}

.card-maps {
	.partnership {
		margin-top: 60px;
	}

	.partnership__title {
		margin-top: 71px;
	}
}
