//_production.scss

.production {
	margin-top: 108px;
	padding-bottom: 77px;
}

.production__title {
	margin-top: 0;
	font-size: 36px;
	line-height: 42px;
}

.production__par {
	font-size: 18px;
	line-height: 26px;
}

.production__slider {
	margin-top: 9px;
}

.production__slide {
	width: 295px;
	height: 179px;
	border-radius: 3px;
	& > img {
		border-radius: 3px;
		object-fit: cover;
		height: 100%;
		width: 100%;
	}
}

.production__arrow {
	width: 35px;
	height: 35px;
	bottom: -65px;
	&.btn-slide--left {
		right: auto;
		left: 50%;
		transform: translateX(calc(-50% - 27px)) rotate(180deg);
	}
	&.btn-slide--right {
		right: auto;
		left: 50%;
		transform: translateX(calc(-50% + 27px));
	}
}


//_partners.scss

.partners__title {
