.banner {
	height: 610px;
	.plus {
		display: none;
	}
}

.banner__ellipse {
	width: 1121.17px;
	height: 1119.17px;
	left: 28%;
	top: 70px;
}

.banner__group-of-squares {
	top: 125px;
	left: 29%;
	.squares {
		margin-bottom: 61.69px !important;
	}
	.square {
		width: 4.81px !important;
		height: 4.81px !important;
		margin-right: 61.69px !important;
	}
}

.banner__item {
	flex-direction: column;
}

.banner__info {
	width: 100%;
	.btn {
		display: none;
	}
}

.banner__title {
	font-size: 36px;
	line-height: 100%;
	margin-top: 49px;
	margin-bottom: 10px;
}

.banner__desc {
	font-size: 16px;
	line-height: 24px;
	margin-bottom: 5px;
}

.banner__slide {
	width: 100%;
	margin: 0;
}

.banner__img {
	width: 266px;
	height: 291px;

}

.banner__slide-desc {
	margin: -2px 0 0;
	font-size: 12px;
	line-height: 20px;
}

.banner__wrap .slick-dots {
	left: 50%;
	transform: translateX(-50%);
}

//monitoring


.monitoring {

}
.monitoring__info {

}

.monitoring__title {

}

.monitoring__par {
	font-size: 16px;
	line-height: 24px;
	padding-bottom: 224px;
}


.monitoring__subtitle {
	font-size: 14px;
	line-height: 16px;
	max-width: 222px;
	margin-bottom: 30px;
}

.monitoring__list {
	height: max-content;
	max-height: max-content;
}

.monitoring__item {
	width: 100%;
	margin-bottom: 20px;
}

.monitoring__images {
	top: 118px;
}

.monitoring__img {
	&::after {
		width: 21px;
		height: 21px;
		font-size: 12px;
		line-height: 100%;
	}
	&--1 {
		width: 90px;
		height: 130px;
		left: 70px;
		&::after {
			bottom: 8px;
		}
	}
	&--2 {
		width: 190px;
		height: 95px;
		left: -1px;
		top: 49px;
		&::after {
			top: 50px;
		}
	}
	&--3 {
		width: 113.98px;
		height: 77.15px;
		top: 180px;
		left: 166px;
		&::after {
			bottom: 9px;
			right: 13px;
		}
	}
	&--4 {
		width: 28px;
		height: 28px;
		top: 157px;
		left: 26px;
		&::after {
			top: -17px;
			left: 18px;
		}
	}
	&--5 {
		width: 43px;
		height: 66px;
		left: 170px;
		top: 138px;
		&::after {
			top: -23px;
			right: -3px;
		}
	}
	&--6 {
		width: 35px;
		height: 20px;
		left: -1px;
		top: 198px;
		&::after {
			top: 7px;
			left: 24px;
		}
	}
}

.monitoring__btn {
	width: 100%;
	margin-top: 12px;
}

