//monitoring


.monitoring {
	padding-top: 0;
}
.monitoring__info {

}

.monitoring__title {
	max-width: 620px;
}

.monitoring__par {
	font-size: 18px;
	line-height: 26px;
	max-width: 537px;
	margin-top: 17px;
}


.monitoring__subtitle {
	font-size: 18px;
	line-height: 21px;
	margin-top: 52px;
	margin-bottom: 48px;
}

.monitoring__list {
	width: 699px;
	min-width: 669px;
}

.monitoring__img {

	&::after {
		width: 43.21px;
		height: 43.21px;
		font-size: 18px;
		line-height: 26px;
	}
	&--1 {
		width: 190px;
		height: 270px;
		left: -5px;
		&::after {
			bottom: 16px;
			left: 4px;
		}
	}
	&--2 {
		width: 399.25px;
		height: 198.76px;
		top: 9px;
		left: -158px;
		&::after {
			top: 107px;
		}
	}
	&--3 {
		width: 239.98px;
		height: 161.15px;
		top: 320px;
		left: 197px;
		&::after {
			bottom: 19px;
			right: 31px;
		}
	}
	&--4 {
		width: 57px;
		height: 52px;
		top: 246px;
		left: -100px;
		&::after {
			top: -38px;
			left: 35px;
		}
	}
	&--5 {
		width: 99px;
		height: 151px;
		left: 195px;
		top: 190px;
		&::after {
			top: -40px;
			right: -5px;
		}
	}
	&--6 {
		width: 68px;
		height: 39px;
		left: -64px;
		top: 362px;
		&::after {
			top: 12px;
			left: 49px;
		}
	}

	img {
		max-width: 100%;
		max-height: 100%;
	}
}

