.header {
	padding: 9px 0 10px;
	background: $color-white;
}
.header__logo {
	width: 100px;
	height: 17.31px;
}

.nav {
	position: relative;
	z-index: 4;
}

.nav__wrap {
	position: absolute;
	top: -1000%;
	left: 0;
	background: $color-white;
	width: 100%;
	flex-direction: column;
	padding: 35px 20px 20px;
	transition: all .7s;
	z-index: -1;
	overflow-y: scroll;
	overflow-x: hidden;
	.nav__wrap-squares {
		display: block;
		position: absolute;
		top: 10px;
		left: 50%;
		.squares {
			margin-bottom: 61px !important;
		}
		.square {
			background: $color-main;
			width: 5px !important;
			height: 5px !important;
			margin-right: 61px !important;
		}
	}
	.nav__list {
		flex-direction: column;
		flex-wrap: wrap;
		height: 210px;
		li {
			width: 50%;
		}
		.nav__item {
			width: 100%;
			font-size: 30px;
			line-height: 100%;
			color: $color-main;
			padding: 15px 0;
			white-space: normal;
		}
	}
	.nav__lang {
		font-size: 24px;
		line-height: 100%;
		color: $color-main;
		padding: 30px 0 15px;
	}
}

.nav__wrap--active {
	top: 45px;
}

.nav__burger {
	display: block;
	height: 28px;
	width: 28px;
	background: none;
	border: none;
	position: relative;
	padding: 0;
	margin: 0;
	cursor: pointer;
	outline: none;
	&-item {
		position: absolute;
		width: 100%;
		height: 2px;
		background: $color-main;
		top: 50%;
		right: 0;
		transform: translateY(-50%);
		transition: all .3s;
		&--one {
			transform: translateY(calc(-50% - 6px));
			width: 14px;
		}
		&--three {
			width: 25px;
			transform: translateY(calc(-50% + 6px));
		}
	}
}

.nav__burger--active {
	.nav__burger-item {
		&--one {
			transform: rotate(-45deg);
			width: 29px;
		}
		&--two {
			opacity: 0;
		}
		&--three {
			width: 29px;
			transform: rotate(45deg);
		}
	}
}


