.service-banner {
	height: 330px;
}

.service-banner__title {
	font-size: 36px;
	line-height: 42px;
	margin-top: 0;
	margin-bottom: 20px;
}

.service-banner__title-two {
	font-size: 30px;
	line-height: 35px;
}

.service-banner__sguares {
	right: -507%;
}

.service-banner__item {
	margin-bottom: 10px;
}
.service-banner__item-title {
	font-size: 14px;
	line-height: 16px;
	width: 140px;
}

.service-banner__item-desc {
	font-size: 16px;
	line-height: 24px;
	width: calc(100% - 140px);
	padding-left: 0;
}

.service-map {
	padding-left: 0;
	#map {
		width: 100%;
		transform: translateX(0);
	}
}
