.footer {
	padding-top: 40px;
}

.footer__wrap {
	max-height: max-content;
	padding-top: 59px;
}

.footer__logo {
	img {
		width: 105px;
		height: 18px;
	}
}

.footer__social {
	width: 21%;
	top: 0;
}

.footer__social-item {
	margin-right: 21px;
}

.footer__social-svg {
	width: 20px;
	height: 20px;
}

.footer__form {
	width: 100%;
	margin-bottom: 49px;
	.btn {
		width: 100%;
		margin-top: 27px;
	}
}

.footer__form-title {
	font-size: 18px;
	line-height: 21px;
	margin-bottom: 16px;
}

.footer__form-input {
	width: 85%;
}

.footer__nav {
	width: 100%;
}

.footer__bottom {
	flex-direction: column-reverse;
	justify-content: flex-start;
	align-items: flex-start;
}

.footer__info {
	position: relative;
	left: auto;
	top: auto;
	right: auto;
	bottom: auto;
	width: 100%;
	margin-bottom: 20px;
}
