.footer {
	position: relative;
	padding-bottom: 20px;
}

.footer__wrap {
	position: relative;
	padding-top: 71px;
	flex-direction: column;
	flex-wrap: wrap;
	max-height: 460px;
}

.footer__logo {
	top: 0;
	left: 0;
	position: absolute;
}

.footer__form {
	width: 50%;
}

.footer__nav {
	width: 50%;
}
.footer__social {
	position: absolute;
	right: 0;
	top: -9px;
	width: 13%;
}
.footer__form-input {
	width: 80%;
}

.footer__checkbox .checkbox__text {
	display: block;
}
.footer__checkbox .checkbox__text > a {
	display: block;
}
.footer__checkbox .checkbox__text::before {
	top: 3px;
}

.checkbox input:checked + .checkbox__text:after {
	top: 1px;
}

.footer__checkbox {
	margin-bottom: 23px;
}

.footer__bottom {
	margin-top: 29px;
}

.footer__nav-lang {
	margin-top: -3px;
}

.footer__info {
	position: absolute;
	bottom: 64px;
	right: -18px;
	font-size: 12px;
	line-height: 14px;
	max-width: 268px;
}

.footer__dev {
	font-size: 12px;
	line-height: 14px;
}
