.contact-banner {
	padding-top: 0;
	height: 370px;
}

.contact-banner__title {
	font-size: 36px;
	line-height: 42px;
	margin-top: 79px;
}


.contact-banner__sguares {
	top: 33px;
	right: -238%;
	.squares {
		margin-bottom: 79px !important;
	}
	.square {
		width: 7px !important;
		height: 6.08px !important;
	}
}
.contact-banner__list {
	padding-top: 2px;
}
.contact-banner__item {
	align-items: flex-start;
	margin-bottom: 20px;
}
.contact-banner__item-title {
	font-size: 18px;
	line-height: 28px;
	width: 270px;
}

.contact-banner__item-desc {
	font-size: 18px;
	line-height: 28px;
	width: calc(100% - 270px);
	max-width: 300px;
}

.content--contact {
	#map {
		height: 290px !important;
	}
}
