//articles

.useful-articles__title {
	margin-top: 40px;
	margin-bottom: 20px;
}

.useful-articles {
	.ui-tabs .ui-tabs-nav li {
		&:last-child {
			margin-left: 82px;
		}
	}
}

.useful-articles__wrap {
	margin-top: 49px;
}


.useful-articles__top-list {

}

.useful-articles__item {
	width: 135px;
}

.useful-articles__bottom {
	margin-top: 34px;
	.useful-articles__item {
		width: 135px;
		&:nth-child(3) {
			display: none;
		}
	}

}

.useful-articles__item-img {
	width: 135px;
	height: 86px;
}

.useful-articles__item-title {
	font-size: 12px;
	line-height: 28px;
	margin-top: 5px;
}

.useful-articles__item-subtitle {
	font-size: 14px;
	line-height: 20px;
	min-height: 80px;
}

.useful-articles__item-desc {
	font-size: 14px;
	line-height: 20px;
	max-width: 120px;
}

.useful-articles__big {

	.useful-articles__item {

	}
	.useful-articles__item-img {
		width: 100%;
		height: 135px;
	}
	.useful-articles__item-title {
		font-size: 12px;
		line-height: 28px;
		margin-top: 6px;
	}
	.useful-articles__item-subtitle {
		font-size: 16px;
		line-height: 24px;
		min-height: 51px;
	}
	.useful-articles__item-desc {
		font-size: 14px;
		line-height: 20px;
		max-width: 100%;
	}
}

//_instruction.scss

