	margin-top: 20px;
}

.useful-documents__item {
	width: 100%;
	margin-bottom: 22px;
}

.useful-documents__item-title {
	min-height: max-content;
}

.useful-documents__item-list {
	li {

		.documents__document {
			margin-bottom: 0;
		}
	}
}

