.production-banner {
	padding-top: 72px;
	height: 289px;
}

.production-banner__title {
	font-size: 36px;
	line-height: 42px;
	margin-top: 0;
}

.production-banner__par {
	font-size: 16px;
	line-height: 24px;
	max-width: 472px;
}

.production-banner__sguares {
	right: -267%;
	top: 40px;
}

.production-banner__ellipse {
	width: 543px;
	height: 543px;
	left: -132px;
	top: -85px;
}


//list

.production-item {
	align-items: flex-end;
	padding-top: 100px;
	&:first-child {
		padding-top: 125px;
	}
}

.production-item__title {
	font-size: 20px;
	line-height: 23px;
}

.production-item__desc {
	font-size: 16px;
	line-height: 24px;
	margin-bottom: 22px;
	max-width: 95%;
}

.production-item__img {
	width: 100%;
	height: 189px;
	& > img {
		right: 25px;
		bottom: 16px;
		max-height: 271px;
	}
}

.production-item--reverse .production-item__info {
	padding-left: 30px;
}

.production-item--reverse .production-item__desc {
	max-width: 100%;
}

.production-item--reverse .production-item__img > img {
	left: 25px;
}

.production-item--reverse .production-item__img > img.production-item__img-min {
	left: 40px;
	max-height: 279px;
}

.partnership {
	margin-top: 100px;
}


