//useful
.useful-banner {
	padding-top: 109px;
	height: 532px;
}

.useful-banner__title {
	font-size: 36px;
	line-height: 42px;
}

.useful-banner__par {
	font-size: 16px;
	line-height: 26px;
}

.useful-banner__sguares {
	right: -253%;
	.squares {
		margin-bottom: 90px !important;
	}
	.square {
		width: 7px !important;
		height: 7px !important;
		margin-right: 90px !important;
	}
}

.useful-banner__ellipse {
	width: 454px;
	height: 454px;
	left: -20px;
	top: 28px;
}

//_diary.scss

.diary__title {
	font-size: 36px;
	line-height: 42px;
}

.diary__wrap {
	margin-top: 30px;
}

.diary__item {
	width: 295px;
	padding: 40px 13px 39px;
	.documents__document {

	}
}

.diary__item-title {
	margin-bottom: 15px;
}

.diary__item-desc {
	margin-bottom: 20px;
}

.diary__img {
	bottom: 0;
	&--parent {
		width: 135px;
		height: 88px;
	}
	&--child {
		right: 5px;
		width: 92px;
		height: 86.59px;
		display: flex;
		justify-content: flex-end;
	}
	& > img {
		max-width: 100%;
		max-height: 100%;
	}
}
//articles

.useful-articles__title {
	font-size: 36px;
	line-height: 42px;
}

.useful-articles__top-list {
	display: none;
}

.useful-articles__item {
	width: 33%;
}

.useful-articles__bottom {
	margin-top: 30px;
	.useful-articles__item {
		width: 31%;
		.useful-articles__item-img {
			& > img {
				border-radius: 3px;
				object-fit: cover;
				height: 100%;
				width: 100%;
			}
		}
		&:last-child {
			display: none;
		}
	}

}

.useful-articles__item-img {
	width: 100%;
	height: 101px;
	& > img {
		border-radius: 3px;
		object-fit: cover;
		height: 100%;
		width: 100%;
	}
}

.useful-articles__item-title {
	font-size: 14px;
	line-height: 28px;
	margin-top: 10px;
}

.useful-articles__item-subtitle {
	font-size: 14px;
	line-height: 20px;
	height: auto;
	min-height: 60px;
}

.useful-articles__item-desc {
	font-size: 14px;
	line-height: 28px;
}

.useful-articles__big {
	width: 100%;

	.useful-articles__item {
		width: 100%;
	}
	.useful-articles__item-img {
		width: 100%;
		height: 289px;
	}
	.useful-articles__item-title {
		margin-top: 8px;
	}
	.useful-articles__item-subtitle {
		height: auto;
		min-height: 29px;
	}
}

//_instruction.scss

.useful-instruction__title {
	font-size: 36px;
	line-height: 42px;
	margin-top: 94px;
	margin-bottom: 31px;
}

.useful-instruction__item {
	max-width: 100%;
	width: 100%;
}

.useful-instruction__item-title {
	font-size: 18px;
	line-height: 26px;
	max-width: 290px;
	margin-bottom: 10px;
}

.useful-instruction__item-img {
	margin-bottom: 20px;
	& > img {
		border-radius: 3px;
		object-fit: cover;
		height: 100%;
		width: 100%;
	}
}
.useful-instruction__item-document .documents__document {
	width: 50%;
}

//_documents.scss

.useful-documents {
	padding-bottom: 54px;
}

.useful-documents__title {
