.breadcrumbs {
	margin-bottom: 46px;
}

.card {
	padding-bottom: 139px;
}

.card-info {
	padding-bottom: 132px;
}

.card__img {
	padding-top: 74px;
	display: flex;
	justify-content: center;
	align-items: flex-start;

	& > img {
		max-width: 420px;
		max-height: 540px;
	}
}

.card__title {
	font-size: 48px;
	line-height: 56px;
	margin-bottom: 41px;
}

.card__subtitle {
	font-size: 24px;
	line-height: 28px;
}

.card__par {
	font-size: 18px;
	line-height: 26px;
}

.card__min-subtitle {
	font-size: 16px;
	line-height: 19px;
	margin-top: 50px;
	margin-bottom: 30px;
}

.card__list {
	li {
		font-size: 18px;
		line-height: 26px;
		margin-bottom: 11px;
	}
}


.card-info__specifications-item {
	margin-bottom: 18px;
	align-items: center;
}

.card-instruction__title {
	margin-bottom: 20px;
}

.card-instruction__video {
	width: 660px;
	height: 288px;
	& > img {
		max-width: 100%;
		max-height: 100%;
	}
}

