.banner {
	height: 458px;
}

.banner__ellipse {
	width: 1121.17px;
	height: 1120.06px;
	left: 35%;
	top: -60px;
}


.banner__group-of-squares {
	left: 42%;
	top: 50px;
	.squares {
		margin-bottom: 61px !important;
	}
	.square {
		width: 4.81px !important;
		height: 4.81px !important;
		margin-right: 61px !important;
	}
}

.banner__info {
	width: 50%;
}

.banner__title {
	font-size: 36px;
	line-height: 100%;
	margin-top: 124px;
}

.banner__desc {
	font-size: 16px;
	line-height: 24px;
	margin-bottom: 31px;
}

.banner__slide {
	width: calc(50% - 24px);
	margin-left: 24px;
	margin-top: 46px;
}

.banner__img {
	width: 290px;
	height: 320px;
	img {

	}
}

.banner__slide-desc {
	font-size: 12px;
	line-height: 20px;
	margin-left: 8px;
	margin-top: 8px;
}
.banner__wrap .slick-dots {
	bottom: 15px;
}
.banner__wrap .slick-dots li {
	width: 8px;
	height: 8px;
	margin: 0 3px;
}
.banner__wrap .slick-dots li.slick-active {
	width: 10px;
	height: 10px;
}

//monitoring

.monitoring__wrap {
	position: relative;
}

.monitoring__info {
	width: 100%;
}

.monitoring__par {
	margin-top: 8px;
	padding-bottom: 472px;
}


.monitoring__subtitle {
	margin-bottom: 40px;
}

.monitoring__list {
	width: 100%;
	min-width: auto;
	height: 150px;
}
.monitoring__item {
	width: 50%;
	margin-bottom: 21px;
}

.monitoring__item-text {
	font-size: 16px;
	line-height: 26px;
	margin-left: 25px;
}

.monitoring__item-number {
	font-size: 18px;
	line-height: 26px;
	bottom: 37%;
}

.monitoring__images {
	position: absolute;
	left: 0;
	top: 115px;
}

.monitoring__img {
	&--1 {
		left: 156px;
	}
	&--2 {
		left: 4px;
	}
	&--3 {
		left: 355px;
	}
	&--4 {
		left: 61px;
	}
	&--5 {
		left: 355px;
	}
	&--6 {
		left: 97px;
	}
}

//advantage


.advantage {
	margin-top: 0;
}

.advantage__wrap {
	flex-direction: column;
	position: relative;
}

.advantage__left {
	width: 100%;
}

.advantage__img {
	position: absolute;
	top: 320px;
	left: 50%;
	transform: translateX(-50%);
	margin-top: 0;
}

.advantage__title {
	max-width: 100%;
	width: 100%;
	margin-bottom: 30px;
}

.advantage__right {
	width: 100%;

}

.advantage__par {
	max-width: 94%;
	padding-bottom: 488px;
}

.advantage__subtitle {
	margin-top: 0;
}

.advantage__list {
	margin-top: 31px;
	max-height: 235px;
	margin-bottom: 14px;
}

.advantage__item {
	margin-bottom: 17px;
}

.advantage__item-title {
	font-size: 18px;
	line-height: 21px;
	margin-bottom: 4px;
}

.advantage__item-desc {
	font-size: 16px;
	line-height: 26px;
}

//about


.about {
	margin-top: 0;
	position: relative;
}

.about__wrap {
	flex-direction: column;
}

.about__left {
	width: 100%;
}

.about__right {
	width: 100%;
	padding-left: 0;
}

.about__title {
	margin-bottom: 32px;
}

.about__par {
	width: 100%;
	margin-bottom: 29px;
}
.about__btn {
	display: none;
}

.about__list {
	padding-top: 291px;
	max-height: 991px;
}

.about__item {
	margin-bottom: 37px;
}

.about__img-bottom {
	position: absolute;
	width: 100%;
	height: 240px;
	top: 471px;
	left: 0;
	& > img {
		object-fit: cover;
		height: 100%;
		width: 100%;
	}
}

//useful-information

.useful-information {
	margin-top: 0;
}

.useful-information__title {
	margin-top: 75px;
}

.useful-information__link-top > span {
	margin-right: 15px;
}

.useful-information__slide {
	width: 278px;
	margin-right: 44px;
}


.useful-information__arrow  {
	width: 35px;
	height: 35px;
	bottom: -52px;
	&.btn-slide--left {
		right: auto;
		left: 50%;
		transform: translateX(calc(-50% - 27px)) rotate(180deg);
	}
	&.btn-slide--right {
		right: auto;
		left: 50%;
		transform: translateX(calc(-50% + 27px));
	}
}

.btn-slide > svg {
	width: 15px;
	height: 15px;
}

// partnership


.partnership {
	margin-top: 152px;
}
.partnership__title {
	margin-top: 75px;
}

.partnership__map {
	height: 235px;
}

//questions

.questions {
	padding-bottom: 49px;
}

.questions__sguares {
	top: 29px;
	left: 45%;
}

.questions__accordion-item {
	width: 100%;
	padding: 20px 10px 9px 30px;
}

.questions__accordion-item .questions__accordion-arrow {
	transform: translateY(5px) rotate(90deg);
}

.questions__accordion-item--active .questions__accordion-arrow {
	transform: translateY(5px) rotate(-90deg);
}


.questions__accordion-desc {
	padding-top: 7px;
	max-width: 100%;
}

.questions__form-title {
	font-size: 30px;
	line-height: 35px;
	margin: 30px 0 10px;
}

.questions__top-inputs {
	width: 100%;
	margin-bottom: 21px;
}

.questions__input-name {
	width: 280px;
}

.questions__input-mail {
	width: 300px;
}

.questions__bottom-inputs {
	width: 100%;
}

.questions__bottom-inputs .questions__textarea {
	width: 100%;
}

.questions__person {
	width: 100%;
}

.questions__btn-wrap {
	margin-top: 26px;
}

