//_partners.scss

.partners {
	padding-top: 0;
	padding-bottom: 59px;
}
.partners__title {
	margin-top: 48px;
}


.partners__par {
	font-size: 16px;
	line-height: 24px;
	margin-top: 20px;
	margin-bottom: 25px;
}


.partners__slide {
	width: 135px;
	height: 105px;
	margin-top: 5px;

	& > div {

		& > img {

		}
	}
}

.partners__arrow {
	&.btn-slide--left {
		right: auto;
		left: 50%;
		transform: translateX(calc(-50% - 27px)) rotate(180deg);
	}
	&.btn-slide--right {
		right: auto;
		left: 50%;
		transform: translateX(calc(-50% + 27px));
	}
}

